import React from 'react'
import './style.scss'


class Sheety extends React.Component {

  constructor(){
    super();
    this.state = {
      error: null,
      isLoaded: false,
      type:[],
      time:[],
      content:[],
      link:[],
    };
  }

  componentDidMount() {
    fetch("https://api.sheety.co/0f98464e-5ae2-4720-9d78-a69acc8eb83c")
      .then(res => res.json())
      .then(
        (result) => {
          this.setState({
            isLoaded: true,
            items: result
          });
        },
        // Note: it's important to handle errors here
        // instead of a catch() block so that we don't swallow
        // exceptions from actual bugs in components.
        (error) => {
          this.setState({
            isLoaded: true,
            error
          });
        }
      )
  }

  render() {
    const { error, isLoaded, items } = this.state;
    if (error) {
      return <div>Error: {error.message}</div>;
    } else if (!isLoaded) {
      return (            
        <div class="spinner">
          <div class="double-bounce1"></div>
          <div class="double-bounce2"></div>
        </div>
      );
    } else {
      return (
          <div className="container"> 
            {items.map(item => (
              <div className="row pl-0">
                <div className="col-11  pl-0 pt-3 pb-1 text-white">
                  <small>{item.type}  &nbsp;|&nbsp;  {item.time}</small>
                </div>
                <div className="col-11  pl-0">
                  <h5><a href={"https://"+item.link} target="blank">{item.content}</a><br/></h5>
                </div>
                
              </div>
            ))}
            <div className="row pl-0 mt-5">
              <div className="col-11  pl-0 text-white">
              </div>
            </div>
          </div>

        );
      }
    }
  }


export default Sheety
